/*******************************************************************************
 * Styles for the base elements of the theme. 
 ******************************************************************************/

@use "layout";
@use "variables";
@use "themes";
@import url("https://cdn.jsdelivr.net/npm/@shoelace-style/shoelace@2.5.2/cdn/themes/light.css");

// Details & Summary

details {
 background: #f9fafb;
 border: 1px solid #e0e0e0;
 border-radius: 3px;
 margin-bottom: 2em;
 padding: 0 1.5em 1em 1.5em;
}

details > summary {
 padding: 6px 0 6px 0;
 width: 100%;
 background: none;
 //color: variables.$link-color;
 //color: #1976d2;
 //color: #3f5177;
 color: #4a5568;
 border: none;
 box-shadow: none;
 font-size: 1.15em;
 font-weight: 400;
 cursor: pointer;
 outline: none;
 border-radius: 6px;
 transition: background 0.18s, color 0.18s;
 list-style: none;
}

details > summary:hover {
 background: #ededed;
}

details[open] > summary {
 border-bottom: 1px solid #e5e7eb;
 margin-bottom: 0.5em;
}

/* Optional: remove default disclosure triangle in WebKit */
details > summary::-webkit-details-marker,
details > summary::marker {
 display: none;
}

details > summary:focus {
 outline: 2px solid #3f5177;
 outline-offset: 2px;
}

.summary-icon {
 transition: transform 0.2s ease-in-out;
 color: #6c757d;
}

details[open] .summary-icon {
 transform: rotate(90deg);
}

// Typography

body {
 font-family: variables.$base-font;
 font-size: 1.03rem;
 letter-spacing: 0.01rem;
}

h1 {
 color: #bf0a30;
 font-size: 32px;
}

h2 {
 font-size: 26px;
}

h3 {
 font-size: 22px;
}

h4 {
 font-size: 19px;
}

blockquote {
 font-family: variables.$base-font;
 font-size: 8px;
}

h5,
h6,
em,
div,
li,
span {
 color: var(--global-text-color);
 font-family: "Montserrat";
 font-weight: 300;
}

nobr {
 white-space: nowrap;
}

hr {
 border-top: 1px solid var(--global-divider-color);
}

table {
 td,
 th {
 font-size: 1rem;
 }

 th {
 font-weight: bold;
 }
}

a {
 //color: #0056b3;
 color: variables.$link-color;
 transition: all 0.2s ease-in-out; // Smooth transition for hover effects
 font-weight: 500;

 &:hover {
 color: variables.$blue-color-dark; // Darker on hover
 text-decoration: none;
 background-color: variables.$link-background-hover; // Light blue background
 border-radius: 2px;
 }

 &:focus {
 outline: 2px solid #01296a; // Add an outline for accessibility
 outline-offset: 2px;
 text-decoration: none;
 }
}

table.table a {
 //color: var(--global-theme-color);
 color: #0056b3;
 /*
 &:hover {
 color: var(--global-theme-color);
 text-decoration: underline;
 }
 */
 &:hover:after :not(.nav-item.dropdown) {
 width: 100%;
 }
}

.table-dark {
 background-color: transparent;
}

figure,
img {
 max-width: 90vw;
}

blockquote {
 background: var(--global-bg-color);
 border-left: 2px solid var(--global-theme-color);
 margin: 1.5em 10px;
 padding: 0em 10px;
 font-size: 1rem;
}

.header-link {
 font-weight: 350;
 &:hover {
 background-color: transparent !important;
 text-decoration: none;
 }
}

// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 font-size: 0.875rem;
 margin-top: 0.75rem;
 margin-bottom: 1.5rem;
 text-align: center;
}

// Card

.card-wrapper {
 display: block;
 text-decoration: none;
 color: inherit;
 position: relative;
 margin-bottom: 1.5rem;
}

.card {
 background-color: var(--global-card-bg-color);

 img {
 width: 95%;
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-text {
 font-size: 1rem;
 color: var(--global-text-color);
 margin-bottom: 0.5em;
 }

 .card-text.keyword {
 margin-top: 10px;
 .keywords-list {
 font-weight: 400;
 span {
 margin-right: 0.5em;
 }
 }
 }

 .card-item {
 width: auto;
 margin-bottom: 0px;

 .row {
 display: flex;
 align-items: center;
 }
 }
}

// Citation

.citation,
.citation-number {
 color: var(--global-theme-color);
}

// Profile

.profile {
 width: 100%;

 .address {
 margin-bottom: 5px;
 margin-top: 5px;
 font-family: Montserrat;

 p {
 display: inline-block;
 margin: 0;
 }
 }
}

.profile.float-right {
 margin-left: 1rem;
}

.profile.float-left {
 margin-right: 1rem;
}

@media (min-width: 576px) {
 .profile {
 width: 25%;

 .address {
 p {
 display: block;
 }
 }
 }
}

.post-description {
 margin-bottom: 2rem;
 font-size: 0.95rem;

 a {
 color: inherit;

 &:hover {
 color: #bf0a30;
 text-decoration: none;
 }
 }
}

// Navbar customization

.navbar {
 box-shadow: none;
 border-bottom: 1px solid var(--global-divider-color);
 background-color: var(--global-bg-color);
 opacity: 0.95;

 // Direct link styles for the navigation menu
 a,
 .nav-link {
 color: var(--global-text-color) !important;
 font-weight: 400 !important;
 background-image: none !important;
 background-color: none !important;
 text-decoration: none !important;
 transition: color 0.2s ease-in-out;

 &:hover {
 color: #bf0a30 !important;
 background-color: var(--global-bg-color) !important;
 text-decoration: none !important;
 }

 &:focus {
 outline: none !important;
 }
 }
}

.navbar .dropdown-menu {
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);

 a:not(.active) {
 color: var(--global-text-color);
 }
 a:hover {
 color: #bf0a30;
 }

 .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
 }
}

.dropdown-item {
 color: var(--global-text-color);

 &:hover {
 color: var(--global-hover-color);
 background-color: var(--global-bg-color);
 }
}

.navbar.navbar-light {
 a {
 color: var(--global-text-color);
 text-decoration: none;

 &:hover {
 text-decoration: none;
 }
 }

 .navbar-brand {
 color: var(--global-text-color);
 }

 .navbar-nav .nav-item .nav-link {
 color: var(--global-text-color);

 &:hover {
 color: var(--global-hover-color);
 }
 }

 .navbar-nav .nav-item.active > .nav-link {
 background-color: inherit;
 font-weight: bolder;
 color: #bf0a30;

 &:hover {
 color: var(--global-hover-color);
 }
 }

 .navbar-brand.social {
 padding-bottom: 0;
 padding-top: 0;
 font-size: 1.7rem;

 a {
 i::before {
 color: var(--global-text-color);
 transition-property: all 0.2s ease-in-out;
 }

 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }
}

.navbar-toggler {
 .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-text-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
 }

 .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
 }

 .middle-bar {
 opacity: 0;
 }

 .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
 }
}

.navbar-toggler.collapsed {
 .top-bar {
 transform: rotate(0);
 }

 .middle-bar {
 opacity: 1;
 }

 .bottom-bar {
 transform: rotate(0);
 }
}

#light-toggle {
 padding: 0;
 border: 0;
 background-color: inherit;
 color: var(--global-text-color);

 &:hover {
 color: var(--global-hover-color);
 }
}

// Social (bottom)

.social {
 text-align: center;

 .contact-icons {
 font-size: 4rem;

 a {
 i::before {
 color: var(--global-text-color);
 transition-property: all 0.2s ease-in-out;
 }

 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }

 .contact-note {
 font-size: 0.8rem;
 }
}

// Footer
footer.fixed-bottom {
 background-color: var(--global-footer-bg-color);
 font-size: 0.75rem;

 .container {
 color: var(--global-footer-text-color);
 padding-top: 9px;
 padding-bottom: 8px;
 }

 a {
 color: var(--global-footer-link-color);

 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

footer.sticky-bottom {
 border-top: 1px solid var(--global-divider-color);
 padding-top: 40px;
 padding-bottom: 40px;
 font-size: 0.9rem;
}

// CV

.cv {
 margin-bottom: 40px;

 .card {
 background-color: var(--global-card-bg-color);
 border: 1px solid var(--global-divider-color);

 .list-group-item {
 background-color: inherit;
 border-color: var(--global-divider-color);

 .badge {
 color: var(--global-card-bg-color) !important;
 background-color: var(--global-theme-color) !important;
 }
 }
 }
}

.table-cv-map {
 background-color: transparent;
 border: none;
 color: var(--global-text-color);
}

// Repositories

@media (min-width: 768px) {
 .repo {
 max-width: 50%;
 }
}

// Blog

.header-bar {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 2rem;
 padding-bottom: 3rem;

 h1 {
 color: var(--global-theme-color);
 font-size: 5rem;
 }
}

.tag-category-list {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 1rem;

 ul {
 justify-content: center;
 display: flow-root;

 p,
 li {
 list-style: none;
 display: inline-block;
 padding: 1rem 0.5rem;
 color: var(--global-text-color-light);
 }
 }
}

.post-title {
 mjx-container[jax="CHTML"][display="true"] {
 display: unset;
 }
}

.post-list {
 margin: 0;
 margin-bottom: 40px;
 padding: 0;

 li {
 border-bottom: 1px solid var(--global-divider-color);
 list-style: none;
 padding-top: 2rem;
 padding-bottom: 2rem;

 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }

 .post-tags {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 padding-top: 0.25rem;
 padding-bottom: 0;
 }

 a {
 color: var(--global-text-color);
 text-decoration: none;

 &:hover {
 color: var(--global-theme-color);
 }
 }

 mjx-container[jax="CHTML"][display="true"] {
 display: unset;
 }
 }
}

.pagination {
 .page-item {
 .page-link {
 color: var(--global-text-color);

 &:hover {
 color: variables.$black-color;
 }
 }

 &.active .page-link {
 color: variables.$white-color;
 background-color: var(--global-theme-color);

 &:hover {
 background-color: var(--global-theme-color);
 }
 }
 }
}

// Distill

.distill {
 a:hover {
 border-bottom-color: var(--global-theme-color);
 text-decoration: none;
 }
}

// Projects

.projects {
 a {
 text-decoration: none;

 &:hover {
 .card-title {
 color: var(--global-theme-color);
 }
 }
 }

 .card {
 img {
 width: 100%;
 }
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }

 .grid-sizer,
 .grid-item {
 width: 250px;
 margin-bottom: 10px;
 }

 h1.category {
 color: var(--global-theme-color);
 /*border-bottom: 1px solid var(--global-divider-color);*/
 padding-top: 0.5rem;
 margin-top: 2rem;
 margin-bottom: 1rem;
 text-align: left;
 }

 h2.category {
 /*color: var(--global-divider-color);*/
 color: rgba(0, 0, 0, 0.4);
 border-bottom: 1px solid var(--global-divider-color);
 padding-top: 0.5rem;
 margin-top: 2rem;
 margin-bottom: 1rem;
 text-align: right;
 }
}

// Team

.team {
 .card-body {
 a:first-of-type {
 text-decoration: none;
 }
 }
}

// Publications

.publications {
 margin-top: 2rem;

 h1 {
 color: var(--global-theme-color);
 font-size: 2rem;
 text-align: center;
 margin-top: 1em;
 margin-bottom: 1em;
 }

 h2 {
 margin-bottom: 1rem;

 span {
 font-size: 1.5rem;
 }
 }

 h2.bibliography {
 color: var(--global-divider-color);
 border-top: 1px solid var(--global-divider-color);
 padding-top: 1rem;
 margin-top: 2rem;
 margin-bottom: -2rem;
 text-align: right;
 }

 ol.bibliography {
 list-style: none;
 padding: 0;
 margin-top: 0;

 li {
 margin-bottom: 1rem;

 .preview {
 width: 100%;
 min-width: 80px;
 max-width: 200px;
 }

 .abbr {
 height: 2rem;
 margin-bottom: 0.5rem;

 abbr {
 display: inline-block;
 background-color: var(--global-theme-color);
 padding-left: 1rem;
 padding-right: 1rem;

 a {
 color: white;

 &:hover {
 text-decoration: none;
 }
 }
 }

 .award {
 color: var(--global-theme-color) !important;
 border: 1px solid var(--global-theme-color);
 }
 }

 .title {
 font-weight: bolder;
 }

 .author {
 a {
 border-bottom: 1px dashed var(--global-theme-color);

 &:hover {
 border-bottom-style: solid;
 text-decoration: none;
 }
 }

 > em {
 border-bottom: 1px solid;
 font-style: normal;
 }

 > span.more-authors {
 color: var(--global-text-color-light);
 border-bottom: 1px dashed var(--global-text-color-light);
 cursor: pointer;

 &:hover {
 color: var(--global-text-color);
 border-bottom: 1px dashed var(--global-text-color);
 }
 }
 }

 .links {
 a.btn {
 color: var(--global-text-color);
 border: 1px solid var(--global-text-color);
 padding-left: 1rem;
 padding-right: 1rem;
 padding-top: 0.25rem;
 padding-bottom: 0.25rem;
 margin-left: 0;

 &:hover {
 color: var(--global-theme-color);
 border-color: var(--global-theme-color);
 }
 }
 }

 .badges {
 padding-bottom: 0.5rem;
 span {
 display: inline-block;
 color: variables.$black-color;
 height: 100%;
 padding-right: 0.5rem;
 vertical-align: middle;

 &:hover {
 text-decoration: underline;
 }
 }
 }

 .hidden {
 font-size: 0.875rem;
 max-height: 0px;
 overflow: hidden;
 text-align: justify;
 transition-property: 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 transition: all 0.15s ease;

 p {
 line-height: 1.4em;
 margin: 10px;
 }

 pre {
 font-size: 1em;
 line-height: 1.4em;
 padding: 10px;
 }
 }

 .hidden.open {
 max-height: 100em;
 transition-property: 0.15s ease;
 -moz-transition: 0.15s ease;
 -ms-transition: 0.15s ease;
 -o-transition: 0.15s ease;
 transition: all 0.15s ease;
 }

 div.abstract.hidden {
 border: dashed 1px var(--global-bg-color);
 }

 div.abstract.hidden.open {
 border-color: var(--global-text-color);
 }
 }
 }
}

// Rouge Color Customization
figure.highlight {
 margin: 0 0 1rem;
}

pre {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 6px;
 padding: 6px 12px;

 pre,
 code {
 background-color: transparent;
 border-radius: 0;
 margin-bottom: 0;
 padding: 0;
 }
}

code {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 3px;
 padding: 3px 3px;
}

// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
 transition: all 750ms !important;
 transition-delay: 0 !important;
}

// Extra Markdown style (post Customization)
.post {
 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 }

 .post-tags {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 padding-top: 0.25rem;
 padding-bottom: 1rem;

 a {
 color: var(--global-text-color-light);
 text-decoration: none;

 &:hover {
 color: var(--global-theme-color);
 }
 }
 }

 .post-content {
 blockquote {
 border-left: 5px solid var(--global-theme-color);
 padding: 8px;

 p {
 margin-bottom: 0;
 }
 }
 }
}

progress {
 /* Positioning */
 position: fixed;
 left: 0;
 top: 56px;
 z-index: 10;

 /* Dimensions */
 width: 100%;
 height: 1px;

 /* Reset the appearance */
 -webkit-appearance: none;
 -moz-appearance: none;
 appearance: none;

 /* Get rid of the default border in Firefox/Opera. */
 border: none;

 /* Progress bar container for Firefox/IE10 */
 background-color: transparent;

 /* Progress bar value for IE10 */
 color: var(--global-theme-color);
}

progress::-webkit-progress-bar {
 background-color: transparent;
}

progress::-webkit-progress-value {
 background-color: var(--global-theme-color);
}

progress::-moz-progress-bar {
 background-color: var(--global-theme-color);
}

.progress-container {
 width: 100%;
 background-color: transparent;
 position: fixed;
 top: 56px;
 left: 0;
 height: 5px;
 display: block;
}

.progress-bar {
 background-color: var(--global-theme-color);
 width: 0%;
 display: block;
 height: inherit;
}

/* Copy button */
.code-display-wrapper {
 position: relative;

 .copy {
 background: var(--global-card-bg-color);
 border-color: var(--global-bg-color);
 border-radius: 0.3rem;
 border-style: none;
 color: var(--global-text-color);
 font-size: medium;
 opacity: 0;
 position: absolute;
 right: 0.5rem;
 top: 0.5rem;
 }

 &:active .copy,
 &:focus .copy,
 &:hover .copy {
 color: var(--global-hover-color);
 opacity: 1;
 }
}

.btn-group.dropdown {
 .btn {
 box-shadow: none;
 -webkit-box-shadow: none;
 }

 .btn-secondary.dropdown-toggle {
 border: 1px solid var(--global-divider-color);

 .page-size {
 color: inherit;
 }

 &:not(.active) {
 background-color: var(--global-bg-color) !important;
 color: var(--global-text-color);
 }

 &:hover {
 background-color: var(--global-hover-color) !important;
 color: var(--global-hover-text-color) !important;
 }
 }

 .dropdown-menu {
 background-color: var(--global-bg-color);
 }

 .dropdown-item {
 background-color: var(--global-bg-color);
 color: var(--global-text-color);

 &:hover {
 color: var(--global-hover-color);
 }
 }

 .dropdown-item.active,
 .dropdown-item:active {
 background-color: var(--global-hover-color);
 color: var(--global-hover-text-color) !important;

 &:hover {
 color: var(--global-hover-text-color);
 }
 }
}

/* Table of Contents */
nav[data-toggle="toc"] {
 top: 5rem;

 .nav .nav > li > a {
 font-size: 0.75rem;
 }

 .nav > li > a {
 color: var(--global-text-color);
 font-size: 0.75rem;

 &:hover {
 color: var(--global-hover-color);
 border-left-color: var(--global-hover-color);
 }
 }

 .nav-link.active {
 color: var(--global-theme-color);
 border-left-color: var(--global-theme-color);
 font-size: 0.75rem;

 &:hover {
 color: var(--global-hover-color);
 border-left-color: var(--global-hover-color);
 }
 }
}

/* small screens */
@media (max-width: 576px) {
 /* override stickyness so that the navigation does not follow scrolling */
 nav[data-toggle="toc"] {
 visibility: hidden;
 height: 0;
 top: 0;
 }
}

/* Tips, warnings, and dangers blockquotes */
blockquote {
 &.block-tip {
 border-color: var(--global-tip-block);
 background-color: var(--global-tip-block-bg);

 p {
 color: var(--global-tip-block-text);
 }

 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
 color: var(--global-tip-block-title);
 }
 }

 &.block-warning {
 border-color: var(--global-warning-block);
 background-color: var(--global-warning-block-bg);

 p {
 color: var(--global-warning-block-text);
 }

 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
 color: var(--global-warning-block-title);
 }
 }

 &.block-danger {
 border-color: var(--global-danger-block);
 background-color: var(--global-danger-block-bg);

 p {
 color: var(--global-danger-block-text);
 }

 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
 color: var(--global-danger-block-title);
 }
 }
}

.featured-posts {
 a {
 color: var(--global-text-color-light);
 text-decoration: none;

 .card-text {
 overflow: hidden;
 text-overflow: ellipsis;
 white-space: nowrap;
 }

 &:hover {
 color: var(--global-theme-color);

 .card-title {
 color: var(--global-theme-color);
 }
 }
 }

 .card-item {
 margin-bottom: 10px;
 }

 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.875rem;
 margin-bottom: 0;
 padding-top: 0.5rem;
 }
}

.grid-container {
 display: grid;
 grid-template-columns: repeat(3, 1fr);
 gap: 10px; /* adjust this for your desired spacing between cards */

 @media (max-width: 768px) {
 grid-template-columns: 1fr;
 }
}

// Customize the primary variant of Shoelace buttons
sl-button[variant="primary"] {
 &::part(base) {
 // Base styles
 color: variables.$blue-color-dark; // text color
 border-color: variables.$blue-color-dark; // border color

 &:hover {
 color: variables.$white-color; // hover text color
 background-color: variables.$blue-color-dark; // hover background color
 }
 }
}

.help-banner {
 display: flex;
 align-items: flex-start;
 gap: 6px;
 padding: 12px;
 width: 100%;
 box-sizing: border-box;
 background-color: #f9fafb;
 border: 1px solid #e5e7eb;
 border-radius: 8px;
 margin: 20px 0;
 margin-bottom: 18px;
 font-size: 0.9rem;
 flex-wrap: wrap;
}

/* Add styles for the icon to keep it from shrinking */
.help-banner i {
 flex-shrink: 0;
 margin-top: 3px; /* Align icon with first line of text */
}

/* Add media query for smaller screens */
@media (max-width: 768px) {
 .help-banner {
 flex-wrap: wrap; /* Allow items to wrap on smaller screens */
 gap: 8px; /* Slightly increase gap for better readability */
 }
}

// **** NEW STYLING FOR TOOLKIT ***** //

/* Filter Section Styles */
.filter-section {
 margin-bottom: 2rem;
}

.filter-section h3 {
 margin-bottom: 1rem;
 color: #333;
 font-size: 1.5rem;
 text-align: center;
}

/* Button Grid Layout */
.button-grid {
 display: grid;
 gap: 1rem;
 margin-bottom: 1rem;
}

.resource-grid {
 grid-template-columns: repeat(4, 1fr);
 grid-template-rows: repeat(2, 1fr);
}

.domain-grid {
 grid-template-columns: repeat(3, 1fr);
}

.subdomain-grid {
 grid-template-columns: repeat(4, 1fr);
}

/* Button Styles */
.filter-btn {
 display: flex;
 align-items: center;
 justify-content: center;
 padding: 0.75rem 1rem;
 border: 2px solid variables.$resource-btn-color-base;
 border-radius: 8px;
 background-color: white;
 color: variables.$resource-btn-color-base;
 font-size: 1rem;
 font-weight: 500;
 transition: all 0.2s ease;
 cursor: pointer;
}

.filter-btn i {
 margin-right: 0.5rem;
}

.filter-btn:hover {
 background-color: variables.$resource-btn-color-base;
 color: white;
}

.filter-btn.active {
 background-color: variables.$resource-btn-color-base;
 color: white;
 transform: scale(1.05);
}

/* Domain button styles */

.domain-btn {
 color: variables.$domain-btn-color-base;
 border-color: variables.$domain-btn-color-base;
}

.domain-btn:hover {
 color: variables.$white-color;
 background-color: variables.$domain-btn-color-base;
 border-color: variables.$domain-btn-color-base;
}

.domain-btn.active {
 color: variables.$white-color;
 background-color: variables.$domain-btn-color-base;
 border-color: variables.$domain-btn-color-base;
}

/* Subdomain button styles */

.subdomain-btn {
 flex: 1;
 margin: 0;
 display: flex;
 flex-direction: column;
 align-items: center;
 justify-content: center;
 text-align: center;
 font-size: 0.8rem;
 padding: 10px 5px;
 border-radius: 8px;
 transition: all 0.3s ease;
 color: variables.$subdomain-btn-color-base;
 background-color: variables.$white-color;
 border: 1.8px solid variables.$subdomain-btn-color-base;

 &:hover {
 color: variables.$subdomain-btn-active-text;
 border-color: variables.$subdomain-btn-color-base;
 background-color: variables.$subdomain-btn-active-background;
 }

 &.active {
 transform: scale(1.05);
 color: variables.$subdomain-btn-active-text;
 border-color: variables.$subdomain-btn-color-base;
 background-color: variables.$subdomain-btn-active-background;
 }
}

/* Reset button styles */
.reset-btn {
 display: block;
 margin: 2rem auto;
 padding: 0.5rem 2.5rem;
 border: 1.8px solid #666;
 border-radius: 6px;
 background-color: white;
 color: #666;
 font-size: 1.08rem;
 transition: all 0.2s ease;
 cursor: pointer;
}

.reset-btn:hover {
 background-color: #666;
 color: white;
}

/* Search Section Styles */
.search-section {
 margin-top: 2rem;
 margin-bottom: 2rem;
}

.search-section h3 {
 margin-bottom: 1rem;
 color: #333;
 font-size: 1.5rem;
 text-align: center;
}

.search-container {
 display: flex;
 gap: 0.5rem;
}

#search-input {
 flex: 1;
 padding: 0.5rem 1rem;
 border: 2px solid #ddd;
 border-radius: 8px;
 font-size: 1rem;
}

.search-btn {
 padding: 0.5rem 1rem;
 border: 2px solid #ddd;
 border-radius: 8px;
 background-color: variables.$white-color;
 color: #333;
 font-size: 1rem;
 cursor: pointer;
 transition: background-color 0.2s ease;
}

.search-btn:hover {
 background-color: variables.$grey-color;
 color: variables.$white-color;
}

/* Responsive adjustments */
@media (max-width: 768px) {
 .resource-grid,
 .domain-grid,
 .subdomain-grid {
 grid-template-columns: repeat(2, 1fr);
 }

 .search-container {
 flex-direction: column;
 }

 .search-btn {
 width: 100%;
 }
}

@media (max-width: 480px) {
 .resource-grid,
 .domain-grid,
 .subdomain-grid {
 grid-template-columns: 1fr;
 }
}
